export default (sequelize, Sequelize) => {
  const Identity = sequelize.define("identity", {
    name_en: {
      type: Sequelize.STRING,
    },
    name_th: {
      type: Sequelize.STRING,
    },
    cover_img_url: {
      type: Sequelize.TEXT,
    },
    file_url: {
      type: Sequelize.TEXT,
    },
    index: {
      type: Sequelize.INTEGER,
    },
  });
  return Identity;
};
